import React from "react";

import ShowDrop from "./ShowDrop";
//used the skeletal frame of previous project then modified it to create Tours Website
const List = ({ answrs, revealer }) => {
  return answrs.map((questbox) => {                 //question
    const { id, rating, name, image, text} = questbox;

    return (
      <article key={id} id={id} className="list">
        
    
        <div style={{ position: "relative" }}>
         

         
        <img className="pfp" src={image} alt="sorry, pal" />
        
         <p>{name}</p>
         <p className="cashbar"><h3><b>${rating}</b></h3></p>
         <br></br>
  

<button className = "noInter" onClick={()=>{ShowDrop(document.getElementById(id))}}>Not interested</button>

          <br></br>
            <p className="answer">{text}</p>
        </div>

        <br></br>
      </article>
    );
  });
};

export default List;
